define('ghost/controllers/setup/two', ['exports', 'ember', 'ic-ajax', 'ghost/mixins/validation-engine'], function (exports, Ember, ic_ajax, ValidationEngine) {

    'use strict';

    /* global md5 */
    exports['default'] = Ember['default'].Controller.extend(ValidationEngine['default'], {
        size: 90,
        blogTitle: null,
        name: null,
        email: '',
        password: null,
        image: null,
        submitting: false,

        ghostPaths: Ember['default'].inject.service('ghost-paths'),
        notifications: Ember['default'].inject.service(),
        application: Ember['default'].inject.controller(),

        gravatarUrl: Ember['default'].computed('email', function () {
            var email = this.get('email'),
                size = this.get('size');

            return 'http://www.gravatar.com/avatar/' + md5(email) + '?s=' + size + '&d=blank';
        }),

        userImage: Ember['default'].computed('gravatarUrl', function () {
            return this.get('image') || this.get('gravatarUrl');
        }),

        userImageBackground: Ember['default'].computed('userImage', function () {
            return 'background-image: url(' + this.get('userImage') + ')';
        }),

        // ValidationEngine settings
        validationType: 'setup',

        actions: {
            setup: function setup() {
                var self = this,
                    notifications = this.get('notifications'),
                    data = self.getProperties('blogTitle', 'name', 'email', 'password');

                notifications.closePassive();

                this.toggleProperty('submitting');
                this.validate({ format: false }).then(function () {
                    ic_ajax.request({
                        url: self.get('ghostPaths.url').api('authentication', 'setup'),
                        type: 'POST',
                        data: {
                            setup: [{
                                name: data.name,
                                email: data.email,
                                password: data.password,
                                blogTitle: data.blogTitle
                            }]
                        }
                    }).then(function () {
                        // Don't call the success handler, otherwise we will be redirected to admin
                        self.get('application').set('skipAuthSuccessHandler', true);

                        self.get('session').authenticate('simple-auth-authenticator:oauth2-password-grant', {
                            identification: self.get('email'),
                            password: self.get('password')
                        }).then(function () {
                            self.set('password', '');
                            self.transitionToRoute('setup.three');
                        });
                    })['catch'](function (resp) {
                        self.toggleProperty('submitting');
                        notifications.showAPIError(resp);
                    });
                })['catch'](function (errors) {
                    self.toggleProperty('submitting');
                    notifications.showErrors(errors);
                });
            }
        }
    });

});