define('ghost/components/gh-notifications', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Component.extend({
        tagName: 'aside',
        classNames: 'gh-notifications',

        notifications: Ember['default'].inject.service(),

        messages: Ember['default'].computed.filter('notifications.content', function (notification) {
            var displayStatus = typeof notification.toJSON === 'function' ? notification.get('status') : notification.status;

            return displayStatus === 'passive';
        })
    });

});