define('ghost/controllers/setup', ['exports', 'ember', 'ic-ajax', 'ghost/mixins/validation-engine'], function (exports, Ember, ic_ajax, ValidationEngine) {

    'use strict';

    exports['default'] = Ember['default'].Controller.extend(ValidationEngine['default'], {
        blogTitle: null,
        name: null,
        email: null,
        password: null,
        submitting: false,

        // ValidationEngine settings
        validationType: 'setup',

        ghostPaths: Ember['default'].inject.service('ghost-paths'),
        notifications: Ember['default'].inject.service(),

        actions: {
            setup: function setup() {
                var self = this,
                    data = self.getProperties('blogTitle', 'name', 'email', 'password'),
                    notifications = this.get('notifications');

                notifications.closePassive();

                this.toggleProperty('submitting');
                this.validate({ format: false }).then(function () {
                    ic_ajax.request({
                        url: self.get('ghostPaths.url').api('authentication', 'setup'),
                        type: 'POST',
                        data: {
                            setup: [{
                                name: data.name,
                                email: data.email,
                                password: data.password,
                                blogTitle: data.blogTitle
                            }]
                        }
                    }).then(function () {
                        self.get('session').authenticate('simple-auth-authenticator:oauth2-password-grant', {
                            identification: self.get('email'),
                            password: self.get('password')
                        });
                    })['catch'](function (resp) {
                        self.toggleProperty('submitting');
                        notifications.showAPIError(resp);
                    });
                })['catch'](function (errors) {
                    self.toggleProperty('submitting');
                    notifications.showErrors(errors);
                });
            }
        }
    });

});