define('ghost/tests/unit/helpers/gh-user-can-test', ['ember-mocha', 'ghost/helpers/gh-user-can'], function (ember_mocha, gh_user_can) {

    'use strict';

    describe('GhUserCanHelper', function () {
        // Mock up roles and test for truthy
        describe('Owner role', function () {
            var user = { get: function get(role) {
                    if (role === 'isOwner') {
                        return true;
                    } else if (role === 'isAdmin') {
                        return false;
                    } else if (role === 'isEditor') {
                        return false;
                    }
                }
            };
            ember_mocha.it(' - can be Admin', function () {
                var result = gh_user_can.ghUserCan([user, 'admin']);
                expect(result).to.equal(true);
            });
            ember_mocha.it(' - can be Editor', function () {
                var result = gh_user_can.ghUserCan([user, 'editor']);
                expect(result).to.equal(true);
            });
        });

        describe('Administrator role', function () {
            var user = {
                get: function get(role) {
                    if (role === 'isOwner') {
                        return false;
                    } else if (role === 'isAdmin') {
                        return true;
                    } else if (role === 'isEditor') {
                        return false;
                    }
                }
            };
            ember_mocha.it(' - can be Admin', function () {
                var result = gh_user_can.ghUserCan([user, 'admin']);
                expect(result).to.equal(true);
            });
            ember_mocha.it(' - can be Editor', function () {
                var result = gh_user_can.ghUserCan([user, 'editor']);
                expect(result).to.equal(true);
            });
        });

        describe('Editor role', function () {
            var user = {
                get: function get(role) {
                    if (role === 'isOwner') {
                        return false;
                    } else if (role === 'isAdmin') {
                        return false;
                    } else if (role === 'isEditor') {
                        return true;
                    }
                }
            };
            ember_mocha.it(' - cannot be Admin', function () {
                var result = gh_user_can.ghUserCan([user, 'admin']);
                expect(result).to.equal(false);
            });
            ember_mocha.it(' - can be Editor', function () {
                var result = gh_user_can.ghUserCan([user, 'editor']);
                expect(result).to.equal(true);
            });
        });

        describe('Author role', function () {
            var user = {
                get: function get(role) {
                    if (role === 'isOwner') {
                        return false;
                    } else if (role === 'isAdmin') {
                        return false;
                    } else if (role === 'isEditor') {
                        return false;
                    }
                }
            };
            ember_mocha.it(' - cannot be Admin', function () {
                var result = gh_user_can.ghUserCan([user, 'admin']);
                expect(result).to.equal(false);
            });
            ember_mocha.it(' - cannot be Editor', function () {
                var result = gh_user_can.ghUserCan([user, 'editor']);
                expect(result).to.equal(false);
            });
        });
    });

});