define('ghost/helpers/gh-user-can', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports.ghUserCan = ghUserCan;

    function ghUserCan(params) {
        if (params[1] === 'admin') {
            return !!(params[0].get('isOwner') || params[0].get('isAdmin'));
        } else if (params[1] === 'editor') {
            return !!(params[0].get('isOwner') || params[0].get('isAdmin') || params[0].get('isEditor'));
        }
        return false;
    }

    exports['default'] = Ember['default'].HTMLBars.makeBoundHelper(ghUserCan);

});