define('ghost/views/application', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].View.extend({
        classNames: 'gh-app',

        toggleSettingsMenuBodyClass: Ember['default'].observer('controller.showSettingsMenu', function () {
            $('body').toggleClass('settings-menu-expanded', this.get('controller.showSettingsMenu'));
        })
    });

});