define('ghost/routes/settings/apps', ['exports', 'ember', 'ghost/routes/authenticated', 'ghost/mixins/current-user-settings', 'ghost/mixins/style-body'], function (exports, Ember, AuthenticatedRoute, CurrentUserSettings, styleBody) {

    'use strict';

    exports['default'] = AuthenticatedRoute['default'].extend(styleBody['default'], CurrentUserSettings['default'], {
        titleToken: 'Apps',

        classNames: ['settings-view-apps'],

        config: Ember['default'].inject.service(),

        beforeModel: function beforeModel(transition) {
            this._super(transition);

            if (!this.get('config.apps')) {
                return this.transitionTo('settings.general');
            }

            return this.get('session.user').then(this.transitionAuthor()).then(this.transitionEditor());
        },

        model: function model() {
            return this.store.find('app');
        }
    });

});