define('ghost/components/gh-alerts', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    var AlertsComponent = Ember['default'].Component.extend({
        tagName: 'aside',
        classNames: 'gh-alerts',

        messages: Ember['default'].computed.filter('notifications', function (notification) {
            var displayStatus = typeof notification.toJSON === 'function' ? notification.get('status') : notification.status;

            return displayStatus === 'persistent';
        }),

        messageCountObserver: Ember['default'].observer('messages.[]', function () {
            this.sendAction('notify', this.get('messages').length);
        })
    });

    exports['default'] = AlertsComponent;

});